import { React, useState, useEffect } from 'react';
import articles from './article-content';
import ArticleList from './../components/ArticleList';
import CommentList from './../components/CommentList';
import NotFoundPage from './NotFoundPage';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentFrom from '../components/AddCommentForm';
const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const [articleInfo, setArticleInfo] = useState({ upVotes: 0, comments: [] });

  useEffect(() => {
    async function fetchData() {
      const result = await fetch(`/api/articles/${name}`);
      const article = await result.json();
      setArticleInfo(article[0]);
    }
    fetchData();
  }, [name]);
  const article = articles.find((a) => a.name === name);
  if (!article) {
    return <NotFoundPage />;
  }
  let otherArticles = articles.filter((a) => a.name !== name);

  return (
    <>
      <h1>{article.title}</h1>
      <UpvoteSection upVotes={articleInfo.upVotes} setArticleInfo={setArticleInfo} articleName={name} />
      {article.content.map((p, key) => (
        <p key={key}>{p}</p>
      ))}
      <CommentList Comments={articleInfo.comments} />
      <AddCommentFrom ArticleName={name} setArticleInfo={setArticleInfo} />
      <h2>Other Articles</h2>
      <ArticleList articles={otherArticles} />
    </>
  );
};

export default ArticlePage;
