import React from 'react';
import { Link } from 'react-router-dom';

const ArticleList = ({ articles }) => {
  return (
    <>
      {articles.map((a, i) => {
        return (
          <Link className="article-list-item" key={i} to={`/article/${a.name}`}>
            <h3>{a.title}</h3>
            <p>{a.content[0].substr(0, 150)}...</p>
          </Link>
        );
      })}
    </>
  );
};

export default ArticleList;
