import React from 'react';
const ArticleList = ({ Comments }) => {
  if (Comments.length) {
    return (
      <>
        <h3>Comments :</h3>
        {Comments.map((cm, i) => {
          return (
            <div className="comment" key={i}>
              <h4>{cm.username}</h4>
              <p>{cm.text}</p>
            </div>
          );
        })}
      </>
    );
  }
  return <></>;
};

export default ArticleList;
